  --color-background: var(--background);
  --color-foreground: var(--foreground);
  --font-sans: var(--font-geist-sans);
  --font-mono: var(--font-geist-mono);
}

@media (prefers-color-scheme: dark) {
  :root {
    --background: #0a0a0a;
    --foreground: #ededed;
  }
}

body {
  background: var(--background);
  color: var(--foreground);
  font-family: Arial, Helvetica, sans-serif;
}

.hover\:bg-\[\#00a884\]\/90:hover {
  background-color: rgb(0 168 132 / 0.9);
}

.hover\:bg-\[\#2a3942\]:hover {
  --tw-bg-opacity: 1;
  background-color: rgb(42 57 66 / var(--tw-bg-opacity, 1));
}

.hover\:bg-\[\#f15c6d\]\/90:hover {
  background-color: rgb(241 92 109 / 0.9);
}

.disabled\:cursor-not-allowed:disabled {
  cursor: not-allowed;
}

.disabled\:bg-\[\#3b4a54\]:disabled {
  --tw-bg-opacity: 1;
  background-color: rgb(59 74 84 / var(--tw-bg-opacity, 1));
}

@media (min-width: 1024px) {

  .lg\:order-1 {
    order: 1;
  }

  .lg\:order-2 {
    order: 2;
  }

  .lg\:max-w-md {
    max-width: 28rem;
  }

  .lg\:grid-cols-2 {
    grid-template-columns: repeat(2, minmax(0, 1fr));
  }
}
